import React, {useState} from "react"
import { TextField, Button } from "@material-ui/core"
import "./CardInput.css"
import { v4 as uuidv4 } from 'uuid'

export default function CardInput({words, setWords}) {
    const [input, setInput] = useState({id:uuidv4(), tr:"", fr:"", example:""})
    const changeTr = (e) => {
        setInput({...input, tr:e.target.value.toLowerCase()})
    }
    const changeFr = (e) => {
        setInput({...input, fr:e.target.value.toLowerCase()})
    }
    const changeExample = (e) => {
        setInput({...input, example:e.target.value})
    }
    const addHandler = (e) => {
        if(!input.tr){
            alert("Trke kelimeyi giriniz")
            return
        }
        if(!input.fr){
            alert("Franszca kelimeyi giriniz")
            return
        }
        if(!input.example){
            alert("rnek kelimeyi giriniz")
            return
        }
        setWords([...words, input])
        setInput({id:uuidv4(), tr:"", fr:"", example:""})
        alert("Kart eklendi")
    }
    return (
    <div className="TextFields">
      <div className="CardInputsField">
        <div className="CardInputField">
          <TextField value={input.fr} onChange={changeFr} id="outlined-basic" label="Franszcas" variant="outlined" />
        </div>
        <div className="CardInputField">
          <TextField value={input.tr} onChange={changeTr} id="outlined-basic" label="Trkesi" variant="outlined" />
        </div>
        <div className="CardInputField">
          <TextField value={input.example} onChange={changeExample} id="outlined-basic" label="rnek Cmle" variant="outlined" />
        </div>
      </div>
      <Button onClick={addHandler} variant="contained" color="primary">
        Ekle
      </Button>
    </div>
  )
}
